import React from 'react';
import { connect } from 'react-redux';
import {HashRouter as Router, Switch, Route, Link, Redirect, useLocation} from 'react-router-dom';
import Info from './info';
import queryString from 'query-string';

const Query = (props) => {
    const location = useLocation();
    console.log(location.search, props)
    props.setAccess(location.search)
    return <div> </div>
}

class Dashboard extends React.Component {
    state = {
        name: '',
        avatar: '',
        hovered: false,
        recentPhotos: new Array,
    }
    setName = (name) => {
        this.setState({
            name: name
        })
    }
    setAvatar = (avatar) => {
        this.setState({
            avatar: avatar
        })
    }
    setPhotos = (photos) => {
        this.setState({
            recentPhotos: photos
        })
        setTimeout(() => {this.forceUpdate()}, 1000)
    }
    setAccess = (a) => {
        let access = document.cookie.match(new RegExp('(^| )' + 'access' + '=([^;]+)'));
        if (!access) {
            console.log('A', a)
            let now = new Date()
            let time = now.getTime()
            time += 1 * 3600 * 1000
            now.setTime(time)
            document.cookie = `access=${a};expires=${now.toUTCString()};path=/;`
            setTimeout(() => {this.forceUpdate()}, 500)
        }
    }
    render() {
        let cookie = document.cookie.match(new RegExp('(^| )' + 'token' + '=([^;]+)'));
        let access = document.cookie.match(new RegExp('(^| )' + 'access' + '=([^;]+)'));
        const {name, avatar, hovered, recentPhotos} = this.state;  
        // will have to hide prop info if not access
        if (cookie) {
            return(
                <div className="dashFlex">
                    <Query setAccess={this.setAccess} />
                    {access &&
                    <React.Fragment>
                    <div class="leftPanel">
                        {recentPhotos.length > 0 && (
                            recentPhotos.map((x,i) => {
                                console.log('a', x)
                                return (
                                    <div key={i} className="widget">
                                        <img src={x.url} />
                                    </div>
                                )
                            })
                        )}
                    </div>
                    <div className="mainSection">
                        <div className="head">
                        <div class="headAvatar"><img src={avatar} /></div>
                        Welcome {name} </div>
                        <Info access={access} setPhotos={this.setPhotos} setName={this.setName} setAvatar={this.setAvatar} />
                    </div>
                    <div className="btnHolder">
                        <Link style={{'textDecoration': 'none'}} to ="/upload">
                            <div style={{'height': 'auto'}} 
                            // onMouseEnter={() => this.setState({hovered: true})}
                            // onMouseLeave={() => this.setState({hovered: false})}
                            // id={!hovered ? "shadow1" : "shadow2"}
                            className="btnDash purple">Schedule an upload</div>
                        </Link>
                        <Link style={{'textDecoration': 'none'}} to ="/scheduled">
                            <div style={{'height': 'auto'}} 
                            // onMouseEnter={() => this.setState({hovered: true})}
                            // onMouseLeave={() => this.setState({hovered: false})}
                            // id={!hovered ? "shadow1" : "shadow2"} 
                            className="btnDash purple">View your scheduled posts</div>
                        </Link>
                        <div style={{'height': 'auto'}} 
                        // onMouseEnter={() => this.setState({hovered: true})}
                        // onMouseLeave={() => this.setState({hovered: false})}
                        // id={!hovered ? "shadow1" : "shadow2"} 
                        className="btnDash purple">Logout of the associated instagram account</div>
                    </div>
                    </React.Fragment>
                    }
                </div>
            )
        } else {
            return <div>ERROR</div>
        }
    }
}

export default connect((state) => ({

}))(Dashboard);